import React from 'react';
import { createMuiTheme, ThemeProvider } from '@material-ui/core/styles';
import { orange } from '@material-ui/core/colors';
import Button from '@material-ui/core/Button';
import {Link} from 'react-router-dom';
import '../componentsCSS/NavBar.css';

/* Currently in use NavBar */
const background = createMuiTheme({
  palette: {
    primary: {
      main: '#87CEEB',
    },
    secondary: {
      main: orange[500],
    },
  },
  shadows: ["none"]
});

const tabsTheme = createMuiTheme({
  palette: {
    primary: {
      main: '#FFFFFF',
    },
    secondary: {
      main: orange[500],
    },
  },
});

const NavBar = () => {
  return (
    <ThemeProvider theme={background}>
        {/*<ToolBar> */}
        <div className="customizeNavBar">
            <div className="navBarTitle">Haoston Huynh</div>
            <div className="navButtonBar">
                <ThemeProvider theme={tabsTheme}>
                <Button component={Link} to="/portfolio" color="primary">Home</Button>
                </ThemeProvider>
            </div>
        </div>
        {/*</ToolBar> */}
    </ThemeProvider>
  )
}

export default NavBar;